.Teste {
	text-align: center;
}

.Teste-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.Teste-logo {
		animation: Teste-logo-spin infinite 20s linear;
	}
}

.Teste-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.Teste-link {
	color: #61dafb;
}

@keyframes Teste-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

