.tema {
	display: flex;
	flex-direction: column;
	justify-content: center;
	font-size: 1.5rem;
	align-items: center;
	text-align: center;
	position: relative;
	width: 100%;
	background-image: url("../images/fundo-raposa.jpg");
	background-repeat: repeat;
}

.bgtema::before {
	content: "";
	position: absolute;
	top: 0;
	right: 0px;
	bottom: 0px;
	left: 0px;

	background-color: rgba(255, 255, 255, 0.6);
}
