.pagamento {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	text-align: center;
	justify-content: center;
	align-items: center;
}

.pagamento-card {
	text-align: start;
	padding: 5px;
	border: 1px solid black;
	border-radius: 4px;
	margin: 10px 0px;
	font-size: 0.8rem;
}

.pix {
	background: lightgreen;
}

.itau {
	background: rgb(245, 184, 71);
}
.nu {
	background: rgb(221, 84, 221);
}
.picpay {
	background: greenyellow;
}
