.nome {
	position: relative;
	/* background-image: url("../images/fundo-raposa2.jpg"); */
	background-image: url("https://i.pinimg.com/originals/6a/32/ac/6a32ac6cf41c07256fdffbd5d1f9d015.jpg");
	/* background-size: 100% 100%; */
	background-size: auto;
	background-repeat: repeat;
	background-attachment: scroll;
}

.nome-bgtema::before {
	content: "";
	position: absolute;
	top: 0;
	right: 0px;
	bottom: 0px;
	left: 0px;

	background-color: rgba(255, 255, 255, 0.5);
}

