.comment-box * {
	font-family: "Yomogi", cursive;
}

.img-center {
	text-align: center;
}

.raposa-recado {
	width: 200px;
	height: 150px;
	margin: 10px;
	border-radius: 50%;
	border: solid black;
}

button {
	border: none;
	border-radius: 0.25rem;
	font-size: 0.75rem;
	font-weight: bold;
	padding: 0.75em;
	display: inline-block;
	text-decoration: none;
}
button:hover {
	cursor: pointer;
}
button:focus {
	text-decoration: none;
	outline: none;
}

.comment-box h2 {
	text-align: center;
}
.comment-box {
	width: 600px;
	max-width: 100%;
	color: rgb(0, 0, 0);
	background: linear-gradient(
		-45deg,
		rgb(236, 164, 105) 0%,
		rgb(95, 156, 197) 100%
	);

	padding: 0.2rem 1rem 2rem;
	justify-content: start;

	font-size: 1rem;
}
.comment-form {
	background-color: #fff;
	border-radius: 0.25rem;
	margin-bottom: 1rem;
	padding: 1rem;
	justify-content: start;
	text-align: start;
}
.comment-form-fields {
	margin-bottom: 0.25rem;
	text-align: start;
}
.comment-form-fields textarea {
	border: none;
	border-bottom: 1px solid #404040;
	font-size: 0.85rem;
	padding: 0.25rem 0;
	width: 99%;
	text-align: start;
	margin-top: 0.5rem;
}
.comment-form-fields textarea:focus {
	border-bottom-color: #00bdfc;
	outline: none;
	text-align: start;
}

.comment-count {
	color: #4d6061;
}
#comment-reveal {
	float: right;
	background-color: #00bdfc;
	color: #fff;
}
.comment {
	border-top: 0.125rem solid #404040;
}
.comment-body {
	font-style: italic;
	margin: 0 0 0 1rem;
	justify-content: start;
	text-align: start;
	color: rgb(0, 0, 0);
}

.comment-resposta {
	font-weight: 600;
	margin: 0 0 0 2rem;
	justify-content: start;
	text-align: start;
	color: rgb(0, 0, 0);
}
.comment-footer {
	margin-bottom: 1rem;
}
.comment-footer-delete {
	padding: 0.2rem 0;
	color: #c90000;
	text-decoration: none;
	background-color: transparent;
	margin-left: 200px;
}
.comment-footer-delete :hover {
	color: #00bdfc;
	font-weight: bold;
}
.comment-footer-responder {
	padding: 0.2rem 0;
	color: blue;
	text-decoration: none;
	background-color: transparent;
	margin-left: 20px;
}
.comment-footer-responder :hover {
	color: #00bdfc;
	font-weight: bold;
}
