.home {
	position: relative;
	background-image: url("https://dmhxz00kguanp.cloudfront.net/fotos/85550/adesivo-de-parede-raposinhas-laranja-189326.jpg");
	background-size: auto;
	background-repeat: repeat;
	background-color: black;
}

.home-bgtema::before {
	content: "";
	position: absolute;
	top: 0;
	right: 0px;
	bottom: 0px;
	left: 0px;

	background-color: rgba(255, 255, 255, 0.65);
}
