.lista-presentes {
	display: flex;
	flex-direction: column;
	background: linear-gradient(to right, rgb(131, 164, 212), rgb(182, 251, 255));
	text-align: center;
}

.cabecalho h1 {
	position: relative;
	color: rgb(0, 0, 0);
	font-weight: 1600;
	font-size: 2.8rem;
	padding: 3%;
}

.raposa-presente {
	width: 150px;
	height: 150px;
}

.lista {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}

.lista-presentes-texto h3 {
	max-width: 680px;
	text-align: justify;
	font-weight: 600;
}
