* {
	color: rgb(0, 0, 0);
	/* font-family: "Courier New", Courier, monospace; */
	/* font-family: "Great Vibes", cursive; */
	font-family: "Yomogi", cursive;
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

.page {
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	height: 100%;
}

.texto {
	justify-content: center;
	font-size: 1.5rem;
	align-items: center;
	text-align: center;
}
.texto h1 {
	position: relative;
	color: rgb(0, 0, 0);
	font-weight: 600;
	font-size: 2.8rem;
	padding: 1%;
}

.texto h2 {
	position: relative;
	color: rgb(0, 0, 0);
	font-weight: 600;
	font-size: 2rem;
	padding: 1%;
}

.texto h3 {
	position: relative;
	color: rgb(0, 0, 0);
	font-size: 1.5rem;
	max-width: 680px;
	font-weight: 600;
	text-align: justify;
	padding: 3%;
}

.pages {
	display: flex;
	height: 90vh;
	align-items: center;
	justify-content: center;
	font-size: 3rem;
}
